import styled from 'styled-components';

export const Container = styled.img`
    max-width: 1000px;
    margin: 0 auto;
    padding: 10px;
    z-index: 100;
`;

export const Img = styled.img`
    width: 25vh;
    border-radius: 50%;
`;

export const IconContainer = styled.div`
    width: 90px;
    margin: 0 auto;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
`;